html,
body {
  height: 100%;
  margin: 0;
  background-color: #eee;
  text-align: center;
  font-family: sans-serif;
}

body {
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}

footer {
  font-size: 15px;
  flex-shrink: 0;
  padding-bottom: 1.5em;
  margin: 0 1.5em;
}

h1 {
  margin: 1em 0 0.5em 0;
  font-weight: normal;
}

h1 + p {
  margin: 1.5em 0;
}

#key {
  font-family: monospace;
  width: 22em;
}

#chart-scroller {
  margin: 1.5em;
  overflow: auto;
}

#chart-container {
  position: relative;
  margin: auto;
  max-width: 80vw;
}

#chart {
  display: none;
  background-color: white;
  border-radius: 5px;
}

@media (max-width: 1400px) {
  #chart-container {
    max-width: calc(90vw - 3em);
  }
}

@media (max-width: 1200px) {
  #chart-container {
    max-width: calc(100vw - 3em);
  }
}

@media (max-width: 1024px) {
  #chart-container {
    min-width: calc(1023px - 3em);
    max-width: none;
  }
}
